html {
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
  transition: color 0.1s;
}

body {
  margin: 0;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}
